import { Mentsu } from '../mentsu';

/**
 *  
 */
export class Toitsu extends Mentsu {
  /*  */
  public getHu(): number {
    let hu = 0;
    if (this.isJihai()) {
      // ?1
      hu = 2;
    }
    if (this.getAgariType() == Mentsu.TANKI) {
      hu += 2;
    }
    return hu;
  }

  /*  */
  public getAgariType(): string {
    if (this.isAgari()) {
      return Mentsu.TANKI;
    }
    return "";
  }
}


