import { Hai } from './models/hai';

/**
 *  
 */
export class HaiMan {
  public static MAN1 = new Hai("011", "");
  public static MAN2 = new Hai("012", "");
  public static MAN3 = new Hai("013", "");
  public static MAN4 = new Hai("014", "");
  public static MAN5 = new Hai("015", "");
  public static MAN6 = new Hai("016", "");
  public static MAN7 = new Hai("017", "");
  public static MAN8 = new Hai("018", "");
  public static MAN9 = new Hai("019", "");

  public static PIN1 = new Hai("021", "");
  public static PIN2 = new Hai("022", "");
  public static PIN3 = new Hai("023", "");
  public static PIN4 = new Hai("024", "");
  public static PIN5 = new Hai("025", "");
  public static PIN6 = new Hai("026", "");
  public static PIN7 = new Hai("027", "");
  public static PIN8 = new Hai("028", "");
  public static PIN9 = new Hai("029", "");
  
  public static SOU1 = new Hai("031", "");
  public static SOU2 = new Hai("032", "");
  public static SOU3 = new Hai("033", "");
  public static SOU4 = new Hai("034", "");
  public static SOU5 = new Hai("035", "");
  public static SOU6 = new Hai("036", "");
  public static SOU7 = new Hai("037", "");
  public static SOU8 = new Hai("038", "");
  public static SOU9 = new Hai("039", "");

  public static TON = new Hai("1aA", "");
  public static NAN = new Hai("1aB", "");
  public static SYA = new Hai("1aC", "");
  public static PE = new Hai("1aD", "");
  public static HAKU = new Hai("1bE", "");
  public static HATSU = new Hai("1bF", "");
  public static TYUN = new Hai("1bG", "");

  /**  */
  public static YAOCHU_HAI_LIST: Array<Hai> = [
    HaiMan.MAN1, HaiMan.MAN9, HaiMan.PIN1, HaiMan.PIN9, HaiMan.SOU1, HaiMan.SOU9
    , HaiMan.TON, HaiMan.NAN, HaiMan.SYA, HaiMan.PE, HaiMan.HAKU, HaiMan.HATSU, HaiMan.TYUN
  ];

  /**  */
  public static ALL_HAI_TYPE_LIST: Array<Hai> = [
    HaiMan.MAN1, HaiMan.MAN2, HaiMan.MAN3, HaiMan.MAN4, HaiMan.MAN5
    , HaiMan.MAN6, HaiMan.MAN7, HaiMan.MAN8, HaiMan.MAN9                // 1:
    , HaiMan.PIN1, HaiMan.PIN2, HaiMan.PIN3, HaiMan.PIN4, HaiMan.PIN5
    , HaiMan.PIN6, HaiMan.PIN7, HaiMan.PIN8, HaiMan.PIN9                // 2:
    , HaiMan.SOU1, HaiMan.SOU2, HaiMan.SOU3, HaiMan.SOU4, HaiMan.SOU5
    , HaiMan.SOU6, HaiMan.SOU7, HaiMan.SOU8, HaiMan.SOU9                // 3:
    , HaiMan.TON, HaiMan.NAN, HaiMan.SYA, HaiMan.PE                     // a:
    , HaiMan.HAKU, HaiMan.HATSU, HaiMan.TYUN                            // b:
  ];

  // private ALL_HAI_TYPE_LIST: Array<Hai> = new Array<Hai>();
  private ALL_HAI_LIST: Array<Hai> = new Array<Hai>();

  constructor() {
    this.init();
  }

  /* public */
  /**
   * ID
   * @param id ID
   */
  public static getHaiById(id: string): Hai {
    return HaiMan.ALL_HAI_TYPE_LIST.find(hai => hai.getId() == id);
  }

  /**
   * 
   * @param type 
   * @param code 
   */
  public static getHaiByTypeCode(type: string, code: string): Hai {
    return HaiMan.ALL_HAI_TYPE_LIST.find(hai => (hai.getType() == type && hai.getCode() == code));
  }

  /**
   * 
   * @param typeName 
   */
  public static getHaiListByTypeName(typeName: string): Array<Hai> {
    let haiList: Array<Hai> = HaiMan.ALL_HAI_TYPE_LIST.filter(hai => {
      return hai.getTypeName() == typeName;
    });
    return haiList;
  }

  /**
   * 
   */
  public static getJihaiList(): Array<Hai> {
    let haiList: Array<Hai> = HaiMan.ALL_HAI_TYPE_LIST.filter(hai => {
      return hai.isJihai() == true;
    });
    return haiList;
  }

  /* private */
  private init() {
    // 4
    HaiMan.ALL_HAI_TYPE_LIST.forEach(hai => {
      for (let i = 0; i < 4; i++) {
        this.ALL_HAI_LIST.push(hai);
      }
    });
  }
}


