/**
 *  
 */
export class Yaku {
  private name: string;  // 
  private yomi: string;		// 
  private han: number;  // 
  private isKuisagri: boolean;  // 

  constructor(name: string, yomi: string, han: number, isKuisagri: boolean) {
    this.name = name;
    this.yomi = yomi;
    this.han = han;
    this.isKuisagri = isKuisagri;
  }

  /*  */
  public getName(): string {
    return this.name;
  }
  /*  */
  public getYomi(): string {
    return this.yomi;
  }
  /*  */
  public getHan(isNaki: boolean): number {
    let han = this.han;
    if (this.isKuisagri && isNaki) {
      han--;
    }
    return han;
  }
  /*  */
  public getScore(): number {
    return 48000;
  }

  /*  */
  public isYakuman(): boolean {
    return this.han >= 13;
  }
}


